.movies .movie {
    width: 43%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    font-weight: 300;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3), 0 -6px 16px -6px rgba(0,0,0,.025)
}

.movie img {
    position: relative;
    top: -40px;
    max-width: 160px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3), 0 -6px 16px -6px rgba(0,0,0,.025)
}

.movie .movie_title, .movie .movie_year {
    font-weight: 300;
    margin: 0;
}

.movie .movie_title {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 25px;
    color: #2c2c2c;
}

.movie .movie_genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie_genres li {
    margin-right: 10px;
    font-size: 14px;
}